import React, { useState, useEffect, useRef } from 'react'
import Taro, { useRouter } from '@tarojs/taro'
import { View, Text, ScrollView } from '@tarojs/components'
import { therapistService } from '@/services/therapist'
import { storeService } from '@/services/store'
import TherapistInfo from './components/TherapistInfo'
import StoreInfo from './components/StoreInfo'
import BookingSelector, { BookingSelectorHandle } from './components/BookingSelector'
import ShoppingCart from './components/ShoppingCart'
import type { Therapist, Store } from '@/types'
import './index.scss'

interface CartItem {
  serviceId: string
  serviceName: string
  duration: number
  price: number
  discountPrice?: number
  date: string
  time: string
  therapistName: string
  therapistAvatar?: string
}

const TherapistBookingPage: React.FC = () => {
  const router = useRouter()
  const { therapistId, storeId, storeName } = router.params
  
  const [therapist, setTherapist] = useState<Therapist | null>(null)
  const [store, setStore] = useState<Store | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')

  // È¢ÑÁ∫¶ÈÄâÊã©Áä∂ÊÄÅ
  const [cartItems, setCartItems] = useState<CartItem[]>([])
  const [selectedService, setSelectedService] = useState<any>(null)
  
  // ÂæÖÂ§ÑÁêÜÊìç‰ΩúÁä∂ÊÄÅÔºàÁî®‰∫éÊí§ÈîÄÂäüËÉΩÔºâ
  const [sessionStartIndex, setSessionStartIndex] = useState<number>(-1) // ËÆ∞ÂΩïÊú¨Ê¨°‰ºöËØùÂºÄÂßãÊó∂ÁöÑË¥≠Áâ©ËΩ¶ÈïøÂ∫¶
  const [isAutoExpanded, setIsAutoExpanded] = useState(false) // ÊòØÂê¶ÊòØËá™Âä®Â±ïÂºÄÁöÑË¥≠Áâ©ËΩ¶
  
  // BookingSelector ÁªÑ‰ª∂ÂºïÁî®
  const bookingSelectorRef = useRef<BookingSelectorHandle>(null)

  // Mock ÊúçÂä°Êï∞ÊçÆ
  const mockServices = [
    { id: '1', name: 'ËÇ©È¢àË∞ÉÁêÜ', duration: 60, price: 128, discountPrice: 98 },
    { id: '2', name: 'ÂÖ®Ë∫´Êé®Êãø', duration: 90, price: 198, discountPrice: 158 },
    { id: '3', name: 'Ë∂≥Â∫ïÊåâÊë©', duration: 45, price: 88 },
    { id: '4', name: 'ÊãîÁΩêÂàÆÁóß', duration: 30, price: 68, discountPrice: 58 },
    { id: '5', name: '‰∏≠ÂåªÁêÜÁñó', duration: 120, price: 298, discountPrice: 238 }
  ]

  useEffect(() => {
    loadData()
  }, [therapistId, storeId])

  const loadData = async () => {
    try {
      setLoading(true)
      setError('')

      const [therapistRes, storeRes] = await Promise.all([
        therapistService.getTherapistDetail(therapistId),
        storeService.getStoreDetail(storeId)
      ])

      setTherapist(therapistRes.data)
      setStore(storeRes.data)
    } catch (err) {
      console.error('Failed to load data:', err)
      setError('Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
    } finally {
      setLoading(false)
    }
  }

  const handleServiceSelect = (service: any) => {
    setSelectedService(service)
  }

  const handleTimeSelect = (date: string, time: string) => {
    if (!selectedService || !therapist) return

    // Â¶ÇÊûúÊòØÊñ∞ÁöÑ‰ºöËØùÔºåËÆ∞ÂΩïÂºÄÂßã‰ΩçÁΩÆ
    if (sessionStartIndex === -1) {
      setSessionStartIndex(cartItems.length)
      setIsAutoExpanded(true)
    }

    const newItem: CartItem = {
      serviceId: selectedService.id,
      serviceName: selectedService.name,
      duration: selectedService.duration,
      price: selectedService.price,
      discountPrice: selectedService.discountPrice,
      date,
      time,
      therapistName: therapist.name,
      therapistAvatar: therapist.avatar
    }

    // Ê£ÄÊü•ÊòØÂê¶Â∑≤Â≠òÂú®Áõ∏ÂêåÊó∂Èó¥ÊÆµÁöÑÈ¢ÑÁ∫¶ÔºàÂú®Êï¥‰∏™Ë¥≠Áâ©ËΩ¶‰∏≠Ôºâ
    const existingIndex = cartItems.findIndex(
      item => item.date === date && item.time === time
    )

    if (existingIndex >= 0) {
      // Êó†ËÆ∫ÊòØÂê¶Âú®ÂΩìÂâç‰ºöËØù‰∏≠ÔºåÈÉΩÁõ¥Êé•ÊõøÊç¢
      const newItems = [...cartItems]
      newItems[existingIndex] = newItem
      setCartItems(newItems)
      
      Taro.showToast({
        title: 'Â∑≤Êõ¥Êñ∞ËØ•Êó∂ÊÆµÈ¢ÑÁ∫¶',
        icon: 'success'
      })
    } else {
      // Ê∑ªÂä†Êñ∞È¢ÑÁ∫¶
      setCartItems([...cartItems, newItem])
      
      Taro.showToast({
        title: 'Â∑≤Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶',
        icon: 'success'
      })
    }
  }

  // Êí§ÈîÄÊìç‰ΩúÔºàÁÇπÂáªÈÅÆÁΩ©Êó∂Ôºâ
  const handleCartMaskClick = () => {
    if (isAutoExpanded && sessionStartIndex >= 0) {
      // Êí§ÈîÄÊú¨Ê¨°‰ºöËØù‰∏≠ÊâÄÊúâÊñ∞Â¢ûÁöÑÈ°π
      const newItems = cartItems.slice(0, sessionStartIndex)
      setCartItems(newItems)
      
      // Ê∏ÖÈô§ÈÄâ‰∏≠ÁöÑÊó∂Èó¥
      bookingSelectorRef.current?.clearSelectedTime()
      
      // ÈùôÈªòÊí§ÈîÄÔºå‰∏çÊòæÁ§∫ÊèêÁ§∫
    }
    
    // ÈáçÁΩÆ‰ºöËØùÁä∂ÊÄÅ
    setSessionStartIndex(-1)
    setIsAutoExpanded(false)
  }

  // Á°ÆËÆ§Êìç‰ΩúÔºàÁÇπÂáª"ÁªßÁª≠È¢ÑÁ∫¶"Êó∂Ôºâ
  const handleCartContinue = () => {
    // Á°ÆËÆ§Êìç‰ΩúÔºåÈáçÁΩÆ‰ºöËØùÁä∂ÊÄÅÔºåÂÖÅËÆ∏ÁªßÁª≠Ê∑ªÂä†
    setSessionStartIndex(-1)
    setIsAutoExpanded(false)
  }

  const handleCheckout = () => {
    if (cartItems.length === 0) return

    // Ê∏ÖÈô§‰ºöËØùÁä∂ÊÄÅ
    setSessionStartIndex(-1)
    setIsAutoExpanded(false)

    // ÂØºËà™Âà∞È¢ÑÁ∫¶Á°ÆËÆ§È°µÈù¢
    const params = {
      therapistId: therapistId!,
      storeId: storeId!,
      items: JSON.stringify(cartItems)
    }
    
    Taro.navigateTo({
      url: `/pages/booking/confirm/index?${Object.entries(params)
        .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
        .join('&')}`
    })
  }

  if (loading) {
    return (
      <View className="therapist-booking-page">
        <View className="loading">Âä†ËΩΩ‰∏≠...</View>
      </View>
    )
  }

  if (error || !therapist || !store) {
    return (
      <View className="therapist-booking-page">
        <View className="error">{error || 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•'}</View>
      </View>
    )
  }

  return (
    <View className="therapist-booking-page">
      <ScrollView className="main-content" scrollY>
        <TherapistInfo 
          therapist={therapist} 
          storeId={storeId as string}
          storeName={storeName as string}
        />
        <StoreInfo store={store} />
        <BookingSelector 
          ref={bookingSelectorRef}
          services={mockServices}
          onServiceSelect={handleServiceSelect}
          onTimeSelect={handleTimeSelect}
        />
      </ScrollView>
      <ShoppingCart 
        items={cartItems}
        therapist={therapist}
        onCheckout={handleCheckout}
        onMaskClick={handleCartMaskClick}
        onContinue={handleCartContinue}
        hasPendingAction={isAutoExpanded && sessionStartIndex >= 0}
      />
    </View>
  )
}

export default TherapistBookingPage